:root {
	--light: #F8F8F8;
	--dark: #313131;
	--grey: #888;
	--primary: #FFCE00;
	--secondary: #FE4880;
	--alert: #FF1E2D;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: montserrat, sans-serif;
}

input, button {
	border: none;
	outline: none;
	background: none;
}

body {
	background-color: #F8F8F8;
	background-color: var(--light);
}

header {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	padding: 15px;
}

h1 {
	color: #888;
	color: var(--grey);
	font-size: 32px;
	font-weight: 600;
	text-align: left;
}

.total-amount {
	color: #888;
	color: var(--grey);
	font-size: 28px;
	font-weight: 900;
	background-color: #DFDFDF;
	padding: 5px 25px;
	border-radius: 8px;
}

.amount-form {
	display: block;
	margin: 15px;
	position: relative;
}

.amount-form:after {
	content: "";
	position: absolute;
	top: 0px;
	left: 0px;
	right: 0px;
	bottom: 0px;
	z-index: 0;
	background-image: linear-gradient(to right, #FFCE00, #FE4880);
	background-image: linear-gradient(to right, var(--primary), var(--secondary));
	border-radius: 10px;
	transition: 0.2s;
}

.amount-form:focus-within:after {
	top: -3px;
	left: -3px;
	right: -3px;
	bottom: -3px;
}
.form-inner {
	position: relative;
	z-index: 1;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	transition: 0.4s;
	border-radius: 8px;
}
.form-inner input {
	font-size: 18px;
	padding: 10px 15px;
	background-color: #FFF;
}

.form-inner input[type="text"] {
	border-radius: 8px 0px 0px 8px;
	-webkit-flex: 1 1 80%;
	        flex: 1 1 80%;
}
.form-inner input[type="date"] {
	border-radius: 0px;
	min-width: 200px;
}
.form-inner input[type="submit"] {
	border-radius: 0px 8px 8px 0px;
	cursor: pointer;
	
	background-image: linear-gradient(to right, #FFCE00 50%, #FFCE00 50%, #FE4880);
	
	background-image: linear-gradient(to right, var(--primary) 50%, var(--primary) 50%, var(--secondary));
	background-size: 200%;
	background-position: 0%;

	color: #313131;

	color: var(--dark);
	font-weight: 600;
	text-transform: uppercase;

	transition: 0.4s;
}

.form-inner input[type="submit"]:hover {
	background-position: 100%;
	color: #FFF;
}

.expense-list {
	padding: 15px;
}

.expense-list .expense-item {
	position: relative;
	padding: 10px 15px;
	background-color: #FFF;
	border-radius: 8px;
	margin-bottom: 15px;
	display: -webkit-flex;
	display: flex;
	transition: 0.4s;
	padding-left: 75px;
}

.expense-list .expense-item .remove-item {
	position: absolute;
	width: 0;
	top: 0;
	left: 0;
	bottom: 0;
	overflow: hidden;

	cursor: pointer;
	display: block;
	color: #F8F8F8;
	color: var(--light);
	font-weight: 600;
	background-color: #FF1E2D;
	background-color: var(--alert);
	border-radius: 8px 0px 0px 8px;
	transition: 0.2s;
}

.expense-list .expense-item:hover .remove-item {
	width: 50px;
}

.expense-list .expense-item .desc {
	-webkit-flex: 1 1 100%;
	        flex: 1 1 100%;
}

.expense-list .expense-item .price,
.expense-list .expense-item .date {
	min-width: 125px;
}
.expense-list .expense-item:hover {
	box-shadow: 0px 0px 6px rgba(0,0,0,0.1);
}

